import React, { Component } from 'react'
import Shipin from './shipin.js';
import Tiku from './tiku.js';
import Myscorll from "../../common/myiscroll/myiscroll"
import './style.scss'

export default class discover extends Component {
  constructor(){
    super();
    this.state = {
      isShow:true
    }
  }
  render() {
    let {isShow} = this.state; 
    let SetComponent = this.state.isShow?
     < Shipin />:< Tiku />;

    return (
      <div className="page" id="discover">
        <div className="dis_tb border-bottom">
          <ul onClick={(e)=>this.setIsShow(e)}>
            <li id="shipin" className={isShow?"active":""}>小微视频</li>
            <li id="tiku" className={!isShow?"active":""}>题库</li>
          </ul>
        </div>
        <Myscorll top="44px">
          { SetComponent }
        </Myscorll>
      </div>
    )
  }

  setIsShow(e){
    if(e.target.id==='tiku'){
      this.setState({
        isShow:false,
      })
    }else{
      this.setState({
        isShow:true,
      })
    }
  }

}
